import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { LessionService } from '../_services/index';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { AppSettings } from '../app.constant';
import { FacebookService, InitParams } from 'ngx-facebook';
declare var jquery: any;
declare var $: any;
declare var CKEDITOR: any;

@Component({
  selector: 'app-view-lession',
  templateUrl: './view-lession.component.html',
  styleUrls: ['./view-lession.component.css']
})
export class ViewLessionComponent implements OnInit {
  lession: any = {};
  private lessionId: any;

  tabs: any = ['description_tab', 'reviews_tab', 'members_tab']

  constructor(private lessionService: LessionService,
    private route: ActivatedRoute, public toastr: ToastsManager, vcr: ViewContainerRef, 
    private router: Router, private fb: FacebookService
  ) {
    this.toastr.setRootViewContainerRef(vcr);

    // facebook
    let initParams: InitParams = {
      appId: '1848389655171361',
      xfbml: true,
      version: 'v2.11'
    };
 
    fb.init(initParams);
  }

  ngOnInit() {
    this.lessionId = this.route.snapshot.params['id'];
    this.getLession(this.lessionId);
    $('html, body').animate({
      scrollTop: $("div.eltdf-content").offset().top
    }, 500);
  }

  private getLession(id: string) {
    this.lessionService.getLessionById(id).subscribe(
      response => {
        this.lession = response;
        console.log(this.lession);
        this.lession.ContentLession = $('<div/>').html(this.lession.ContentLession).text();
        this.lession.ContentLession.replace(/\"/g, "'");
        this.lession.ContentLession.replace(/(\r\n|\n|\r)/gm, " ") + '"';
        this.showContentLession(this.lession);
      }, err => {
        this.toastr.error('Some thing went wrong!', 'Oops!')
      }
    )
  }

  private showContentLession(lession: any) {
    setTimeout(function () {
      $('#DescEditor').html(lession.ContentLession);
    }, 1000);
  }

  private DoPractice(id: any){
    this.router.navigateByUrl('/do-practice/' + id);
  }

  private chooseTab(tab: string) {
    switch (tab) {
      case this.tabs[0]:
        $('.' + this.tabs[0]).addClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[1]).removeClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[2]).removeClass('ui-tabs-active ui-state-active');
        $('#' + this.tabs[0]).show();
        $('#' + this.tabs[1]).hide();
        $('#' + this.tabs[2]).hide();
        break;
      case this.tabs[1]:
        $('.' + this.tabs[1]).addClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[0]).removeClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[2]).removeClass('ui-tabs-active ui-state-active');
        $('#' + this.tabs[1]).show();
        $('#' + this.tabs[0]).hide();
        $('#' + this.tabs[2]).hide();
        break;
      case this.tabs[2]:
        $('.' + this.tabs[2]).addClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[1]).removeClass('ui-tabs-active ui-state-active');
        $('.' + this.tabs[0]).removeClass('ui-tabs-active ui-state-active');
        $('#' + this.tabs[2]).show();
        $('#' + this.tabs[1]).hide();
        $('#' + this.tabs[0]).hide();
        break;
    }
  }

}



