import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { patch } from 'webdriver-js-extender';

@Component({
  selector: 'app-user-dashboard',
  templateUrl: './user-dashboard.component.html',
  styleUrls: ['./user-dashboard.component.css']
})
export class UserDashboardComponent implements OnInit {
  private currentPath: string = '';
  private displayDashboard: boolean = false;
  private listPage = ['become-an-instructor', 'account-profile',
    'list-lesson', 'lessons-wishlist'
  ];

  private displayPage = [false, false, false, false];

  constructor(private _router: Router) {
  }

  ngOnInit() {
    if (this._router.url.split('/')[2] != undefined) {
      this.currentPath = this._router.url.split('/')[2];
      this.displayPage = [false, false, false, false];
      let pathIndex = this.listPage.indexOf(this.currentPath);
      if (pathIndex == -1) {
        this.displayDashboard = true;
      } else {
        this.displayPage[pathIndex] = true;
      }
    } else {
      this.displayDashboard = true;
    }
  }

  goToPage(path: string) {
    let nav = ['/user-dashboard/' + path];
    this._router.navigate(nav);
    this.displayPage = [false, false, false, false];
    let pathIndex = this.listPage.indexOf(path);
    if (pathIndex == -1) {
      this.displayDashboard = true;
    } else {
      this.displayDashboard =false;
      this.displayPage[pathIndex] = true;
    }
  }

}



