import { Component, OnInit, Input, SimpleChange } from '@angular/core';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { PagerService, LessionService } from '../_services/index';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { HttpParams } from '@angular/common/http';
import * as _ from 'underscore';
import { debug } from 'util';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-series-lession',
  templateUrl: './series-lession.component.html',
  styleUrls: ['./series-lession.component.css']
})
export class SeriesLessionComponent implements OnInit, OnChanges {
  @Input() teacherId: string;
  lessions: any = [];
  sort: string; page: any; amount: any;
  nameBtnCreate: string;
  allDatas: any = {};
  pager: any = {};
  pagedItems: any[];
  keysearch: string;
  
  totalRecord: number = 0;
  startItem: number = 1;
  endItem: number = 0;

  gopage: string;
  urlPage: number = 1;
  gridShow: boolean = true;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private lessionService: LessionService, private pagerService: PagerService,
    private router: Router, private route: ActivatedRoute) {
    this.sort = 'Name';
    this.page = 0;
    this.amount = 9;
    this.keysearch = '';
  }

  ngOnInit() {
    this.urlPage = this.route.snapshot.params['page'];
    this.loadAllLession(this.sort, this.urlPage - 1, this.amount, this.keysearch);
    this.setVisiblePage(Number(this.urlPage));
  }

  ngOnChanges(changes: {[propKey: string]: SimpleChange}){
    for (let propName in changes) {
      let changedProp = changes[propName];
      let to = JSON.stringify(changedProp.currentValue);
    }
  }

  private loadAllLession(sort: string, page: any, amount: any, keysearch: any) {
    this.spinnerService.show();
    this.lessionService.getAllLession(sort, page, amount, keysearch).subscribe(data => {
      this.lessions = data.Data;
      this.pagedItems = data.Data;
      this.allDatas = data;
      this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
      this.page = page;
      this.spinnerService.hide();
      this.startItem = (page) * 9 + 1;
      this.endItem = (page) * 9 + data.Data.length;
      this.totalRecord = data.TotalRecord;
      setTimeout(() => {
        this.chooseGridShow(this.gridShow);
      }, 100);
      $('html, body').animate({
        scrollTop: $("div.wpb_wrapper").offset().top
      }, 500);
      this.getImageFromLession(this.lessions);
    }, err => {
      this.spinnerService.show();
    });
  }

  getImageFromLession(list: any) {
    list.forEach(function (element) {
      var content = $('<div/>').html(element.ContentLession).text();
      content.replace(/\"/g, "'");
      content.replace(/(\r\n|\n|\r)/gm, " ") + '"';
      var tmp = document.createElement('div');
      tmp.innerHTML = content;
      var elem = tmp.getElementsByTagName('img')[0];
      if (elem != undefined) {
        element.image = elem['src'];
      } else {
        element.image = "https://www.ezone.ae/wp-content/uploads/2016/09/Learning-English.png";
      }
      // process description
      if (element.Description.length > 60) {
        element.Description = element.Description.substring(0, 50) + "...";
      }
    });
    this.lessions = list;
  }

  retrieveProp(htmlString: any, from: any, prop: any) {
    debugger

  }

  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.loadAllLession(this.sort, page - 1, this.amount, this.keysearch);
    this.setVisiblePage(Number(page));
    this.router.navigateByUrl('/series-lession/' + page);
  }

  private setVisiblePage(page: number) {
    if (Number(page) == 1) {
      $('#firstPage').hide();
      $('#prevPage').hide();
    } else {
      $('#firstPage').show();
      $('#prevPage').show();
    }
    if (Number(page) == this.pager.totalPages) {
      $('#lastPage').hide();
      $('#nextPage').hide();
    } else {
      $('#lastPage').show();
      $('#nextPage').show();
    }
  }

  gotoPage() {
    if (this.gopage != "") {
      this.setPage(+this.gopage);
    } else {
      this.setPage(1);
    }
  }

  goSearch() {
    this.setPage(1);
  }

  viewLession(id: string) {
    this.router.navigateByUrl('/view-lession/' + id);
  }

  private viewTeacher(id: string){
    this.router.navigateByUrl('/teacher-profile/' + id);
  }

  private chooseGridShow(grid: boolean) {
    this.gridShow = grid;
    if (this.gridShow) {
      $('#gridList').addClass('eltdf-active');
      $('#listList').removeClass('eltdf-active');
      $('#grid-show').removeClass('eltdf-cl-simple');
      $('#grid-show').addClass('eltdf-cl-gallery');
      $(".series-image").css("cssText", "height: 170px !important;");
    } else {
      $('#gridList').removeClass('eltdf-active');
      $('#listList').addClass('eltdf-active');
      $('#grid-show').removeClass('eltdf-cl-gallery');
      $('#grid-show').addClass('eltdf-cl-simple');
      $(".series-image").css("cssText", "height: 250px !important;");
    }
  }

}



