import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { AppSettings } from '../app.constant';
import 'rxjs/add/operator/map'

@Injectable()
export class AccountService {
    constructor(private http: Http) { }

    create(account: any) {
        return this.http.post(AppSettings.API_ENDPOINT + 'Learners/Create', account, this.jwt()).map((response: Response) => response.json());
    }

    getProfile() {
        return this.http.get(AppSettings.API_ENDPOINT + 'Accounts/GetProfile', this.jwt()).map((response: Response) => response.json());
    }

    beAnInstructor(account: any) {
        return this.http.put(AppSettings.API_ENDPOINT + 'Learners/UpdateInstructor', account).map((response: Response) => response.json());
    }

    getInfoLearner(){
        return this.http.get(AppSettings.API_ENDPOINT + 'Learners/GetProfileInstructor', this.jwt()).map((response: Response) => response.json());
    }

    // private helper methods

    private jwt() {
        let headers = new Headers({
            'Content-Type': 'application/json',
            'authorization': localStorage.token_type + ' ' + localStorage.access_token
        });
        return new RequestOptions({ headers: headers });
    }

    private jwtLogin() {
        let headers = new Headers({
            'Content-Type': 'application/x-www-form-urlencoded'
        });
        return new RequestOptions({ headers: headers });
    }

}


