import { Component, ViewContainerRef, OnInit } from '@angular/core';
import { AppSettings } from './app.constant';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import {
    PagerService, CategoryService,
    AccountService, ExamQuestionsService
} from './_services/index';

import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { error } from 'selenium-webdriver';

declare var jquery: any;
declare var $: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
    title = 'app';
    account: any = {};
    userLogin: any = {};
    userIsLogged: boolean = false;
    userLogged: any = {};

    constructor(private accountService: AccountService, private http: Http,
        public toastr: ToastsManager, vcr: ViewContainerRef) {
        this.toastr.setRootViewContainerRef(vcr);
    }

    ngOnInit() {
        this.getInfoUser();
    }

    getInfoUser() {
        var token = localStorage.getItem('access_token');
        if (token) {
            this.accountService.getProfile().subscribe(
                response => {
                    localStorage.setItem('IdLogger', response.Id);
                    localStorage.setItem('FirstName', response.FirstName);
                    localStorage.setItem('LastName', response.LastName);
                    localStorage.setItem('PhoneNumber', response.PhoneNumber);
                    this.userIsLogged = true;
                    this.userLogged = response;
                }, error => {

                }
            );
        }
    }

    login() {
        let url = AppSettings.API_ENDPOINT + 'Accounts/token';
        let body = "username=" + this.userLogin.username + "&password=" + this.userLogin.password + "&grant_type=password";
        let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        let options = new RequestOptions({ headers: headers });

        this.http.post(url, body, options).subscribe(
            response => {
                localStorage.setItem('access_token', response.json().access_token);
                localStorage.setItem('expires_in', response.json().expires_in);
                localStorage.setItem('token_type', response.json().token_type);
                this.toastr.success('Login successfully!', 'Yeah!');
                this.getInfoUser();
                $('#loginModal').modal('hide');
            },
            error => {
                this.toastr.error(error.json().error_description, 'Oops!');
            }
        );
    }

    registerUser() {
        this.accountService.create(this.account).subscribe(
            response => {
                this.toastr.success('Please confirm your email ' + response.FirstName, 'Success!');
                $('#registerModal').modal('hide');
            },
            error => {
                this.toastr.error(error.json().error_description, 'Oops!');
            }
        );
    }

    formLogin() {
        $('#loginModal').modal();
    }

    formRegister() {
        $('#registerModal').modal();
    }

    formBeInstructor(){
        $('#becomeInstrcutorModal').modal();
    }

    logout() {
        localStorage.removeItem('access_token');
        localStorage.removeItem('expires_in');
        localStorage.removeItem('token_type');
        this.userIsLogged = false;
    }

    openSideSearch() {
        $('.home-page').addClass('eltdf-right-side-menu-opened');
        $('.eltdf-side-menu-button-opener').addClass('opened');
    }
    closeSideSearch() {
        $('.home-page').removeClass('eltdf-right-side-menu-opened');
        $('.eltdf-side-menu-button-opener').removeClass('opened');
    }

}



