import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';

import { routing } from './app.routing';
import { SeriesLessionComponent } from './series-lession/series-lession.component';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { ToastModule } from 'ng2-toastr/ng2-toastr';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
// service
import {
  PagerService, CategoryService, LessionService,
  ConstantService, ExamQuestionsService, AccountService,
  PracticeService, QuestionsPagerService
} from './_services/index';
import { ViewLessionComponent } from './view-lession/view-lession.component';
import { DoPracticeComponent } from './do-practice/do-practice.component';
import { ConfirmEmailComponent } from './confirm-email/confirm-email.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { FacebookModule } from 'ngx-facebook';
import { TeacherProfileComponent } from './teacher-profile/teacher-profile.component';
import { UserDashboardComponent } from './user-dashboard/user-dashboard.component';
import { BecomeAnInstructorComponent } from './user-dashboard/become-an-instructor/become-an-instructor.component';
import { AccountProfileComponent } from './user-dashboard/account-profile/account-profile.component';
import { ListLessonComponent } from './user-dashboard/list-lesson/list-lesson.component';
import { LessonsWishlistComponent } from './user-dashboard/lessons-wishlist/lessons-wishlist.component';
import { CreateLessonComponent } from './create-lesson/create-lesson.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    SeriesLessionComponent,
    ViewLessionComponent,
    DoPracticeComponent,
    ConfirmEmailComponent,
    PageNotFoundComponent,
    TeacherProfileComponent,
    UserDashboardComponent,
    BecomeAnInstructorComponent,
    AccountProfileComponent,
    ListLessonComponent,
    LessonsWishlistComponent,
    CreateLessonComponent
  ],
  imports: [
    BrowserModule,
    routing,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    ToastModule.forRoot(),
    Ng4LoadingSpinnerModule.forRoot(),
    FacebookModule.forRoot()
  ],
  providers: [PagerService, CategoryService,
    ConstantService, ExamQuestionsService,
    AccountService, LessionService, PracticeService,
    QuestionsPagerService],
  bootstrap: [AppComponent]
})
export class AppModule { }



