import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { PagerService } from '../_services/index';
import { LessionService } from '../_services/lession.service';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-teacher-profile',
  templateUrl: './teacher-profile.component.html',
  styleUrls: ['./teacher-profile.component.css']
})
export class TeacherProfileComponent implements OnInit {

  teacherId: string = '';
  lessions: any = [];
  sort: string; page: any; amount: any;
  nameBtnCreate: string;
  allDatas: any = {};
  pager: any = {};
  pagedItems: any[];
  keysearch: string;
  gopage: string;

  gridShow: boolean = true;
  totalRecord: number = 0;
  startItem: number = 1;
  endItem: number = 0;

  constructor(private route: ActivatedRoute, private spinnerService: Ng4LoadingSpinnerService,
    private lessionService: LessionService, private pagerService: PagerService,
    private router: Router) {
    this.sort = 'Name';
    this.page = 0;
    this.amount = 9;
    this.keysearch = '';
  }

  ngOnInit() {
    this.teacherId = this.route.snapshot.params['id'];
    this.loadAllLession(this.sort, this.page, this.amount, this.keysearch);
  }

  private loadAllLession(sort: string, page: any, amount: any, keysearch: any) {
    this.spinnerService.show();
    this.lessionService.getAllLessionTeacher(this.teacherId, sort, page, amount, keysearch).subscribe(data => {
      this.lessions = data.pagination.Data;
      this.pagedItems = data.pagination.Data;
      this.allDatas = data.pagination;
      this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
      this.page = page;
      this.spinnerService.hide();
      this.startItem = (page) * 9 + 1;
      this.endItem = (page) * 9 + data.pagination.Data.length;
      this.totalRecord = data.pagination.TotalRecord;
      this.getImageFromLession(this.lessions);
      setTimeout(() => {
        this.chooseGridShow(this.gridShow);
      }, 100);
      $('html, body').animate({
        scrollTop: $("div.eltdf-wrapper").offset().top
      }, 500);
    }, err => {
      this.spinnerService.show();
    });
  }

  getImageFromLession(list: any) {
    list.forEach(function (element) {
      var content = $('<div/>').html(element.ContentLession).text();
      content.replace(/\"/g, "'");
      content.replace(/(\r\n|\n|\r)/gm, " ") + '"';
      var tmp = document.createElement('div');
      tmp.innerHTML = content;
      var elem = tmp.getElementsByTagName('img')[0];
      if (elem != undefined) {
        element.image = elem['src'];
      } else {
        element.image = "https://www.ezone.ae/wp-content/uploads/2016/09/Learning-English.png";
      }
      // process description
      if (element.Description.length > 60) {
        element.Description = element.Description.substring(0, 50) + "...";
      }
    });
    this.lessions = list;
  }

  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.loadAllLession(this.sort, page - 1, this.amount, this.keysearch);
    this.setVisiblePage(Number(page));
  }

  private setVisiblePage(page: number) {
    if (Number(page) == 1) {
      $('#firstPage').hide();
      $('#prevPage').hide();
    } else {
      $('#firstPage').show();
      $('#prevPage').show();
    }
    if (Number(page) == this.pager.totalPages) {
      $('#lastPage').hide();
      $('#nextPage').hide();
    } else {
      $('#lastPage').show();
      $('#nextPage').show();
    }
  }

  gotoPage() {
    if (this.gopage != "") {
      this.setPage(+this.gopage);
    } else {
      this.setPage(1);
    }
  }

  goSearch() {
    this.setPage(1);
  }

  private chooseTab(tab: number) {
    if (tab == 0) {
      $('#tab-courses').show();
      $('#tab-curriculum').hide();
      $('#tab-courses').addClass('eltdf-visible');
      $('#tab-curriculum').removeClass('eltdf-visible');
      $('#title-tab-coures').addClass('ui-state-active');
      $('#title-tab-curriculum').removeClass('ui-state-active');
    }
    if (tab == 1) {
      $('#tab-courses').hide();
      $('#tab-curriculum').show();
      $('#tab-curriculum').addClass('eltdf-visible');
      $('#tab-courses').removeClass('eltdf-visible');
      $('#title-tab-coures').removeClass('ui-state-active');
      $('#title-tab-curriculum').addClass('ui-state-active');
    }
  }

  private chooseGridShow(grid: boolean) {
    this.gridShow = grid;
    if (this.gridShow) {
      $('#gridList').addClass('eltdf-active');
      $('#listList').removeClass('eltdf-active');
      $('#grid-show').removeClass('eltdf-cl-simple');
      $('#grid-show').addClass('eltdf-cl-gallery');
      $(".series-image").css("cssText", "height: 170px !important;");
    } else {
      $('#gridList').removeClass('eltdf-active');
      $('#listList').addClass('eltdf-active');
      $('#grid-show').removeClass('eltdf-cl-gallery');
      $('#grid-show').addClass('eltdf-cl-simple');
      $(".series-image").css("cssText", "height: 250px !important;");
    }
  }

  private viewLession(id: string) {
    this.router.navigateByUrl('/view-lession/' + id);
  }

}



