import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { LessionService, AccountService, PracticeService, QuestionsPagerService } from '../_services/index';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { AppSettings } from '../app.constant';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { fail } from 'assert';
import { element } from 'protractor';
import swal from 'sweetalert'
import { setTimeout } from 'timers';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-do-practice',
  templateUrl: './do-practice.component.html',
  styleUrls: ['./do-practice.component.css']
})
export class DoPracticeComponent implements OnInit {
  practice: any = {};
  private practiceId: any;
  timeDoingMinutes: any = "00";
  timeDoingSeconds: any = "00";
  oldScore: string = "checking...";
  totalSeconds: any = 0;
  started: boolean = false;
  userIsLogged: boolean = false;
  showSaveScore: boolean = false;
  doAgain: boolean = false;
  showSubmit: boolean = false;
  showExplain: boolean = false;
  couter: any;
  doExam: any = {};
  answers: any = [];
  answersChoosen: any = [];

  // pager
  // pager object
  pager: any = {};
  pageSize: number = 5;
  currentPage: number = 1;

  // paged items
  pagedItems: any[];

  constructor(private route: ActivatedRoute, private lessionService: LessionService,
    public toastr: ToastsManager, vcr: ViewContainerRef, private accountService: AccountService,
    private practiceService: PracticeService, private spinnerService: Ng4LoadingSpinnerService,
    private _location: Location, private questionsPagerService: QuestionsPagerService) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    this.practiceId = this.route.snapshot.params['id'];
    this.checkUserLogged();
  }

  private checkUserLogged() {
    this.spinnerService.show();
    var token = localStorage.getItem('access_token');
    if (token) {
      this.accountService.getProfile().subscribe(
        response => {
          this.userIsLogged = true;
          this.doExam.LearnerId = response.Id;
          this.getPractice(this.practiceId);
        }, error => {
          this.spinnerService.hide();
          this.toastr.warning('Please login before do this practice');
          setTimeout(() => {
            this._location.back();
          }, 1500);
        }
      );
    }
    else {
      this.spinnerService.hide();
      this.toastr.warning('Please login before do this practice');
      setTimeout(() => {
        this._location.back();
      }, 1500);
    }
  }

  private getPractice(id: string) {
    this.practiceService.getPractice(id).subscribe(
      response => {
        this.practice = response;
        this.doExam.LessionPracticeId = response.Id;
        this.oldScore = this.practice.OldScore;
        this.spinnerService.hide();
        this.setPage(this.currentPage);
      }, err => {
        this.spinnerService.hide();
        this.toastr.error('Some thing went wrong!', 'Oops!');
      }
    );
  }

  private startDoing() {
    this.started = true;
    this.showSubmit = true;
    this.couter = setInterval(() => {
      this.setTime();
    }, 1000);
  }

  private setTime() {
    this.totalSeconds++;
    this.timeDoingSeconds = this.pad(this.totalSeconds % 60).toString();
    this.timeDoingMinutes = this.pad(Math.floor(this.totalSeconds / 60));
  }

  private pad(val) {
    var valString = val + "";
    if (valString.length < 2) {
      return "0" + valString;
    } else {
      return valString;
    }
  }

  private submitDoing() {
    clearInterval(this.couter);
    let checkAnswers = this.checkAnswers();
    if (checkAnswers) {
      this.answersChoosen.forEach((element, index) => {
        let answer = { "QuestionId": element.Id, "Answer": element.answer };
        this.answers.push(answer);
      });
      this.pageSize = this.practice.Questions.length;
      this.setPage(1);
      this.showSaveScore = true;
      this.doAgain = true;
      this.showSubmit = false;
      this.doExam.TimeFinished = this.totalSeconds;
      this.doExam.Answers = this.answers;
      setTimeout(() => {
        $('.panel-primary').find('input').attr('disabled', true);
        this.getScoreOfUser();
      }, 200);
    }
    else {
      this.toastr.warning('Bn hy hon thnh bi tp trc khi np bi');
    }
  }

  private getScoreOfUser() {
    let score = 0;
    this.answersChoosen.forEach((element, index) => {
      if (element.answer == this.practice.Questions[index].CorrectAnswer) {
        score += 1;
      } else {
        this.practice.Questions[index].wrongAnswer = element.answer;
      }
    });
    let result = "Bn  lm ng " + score + " trong tng s " + this.practice.Questions.length + " cu hi!";
    if ((this.practice.Questions.length / 2) <= score) {
      swal(score + " / " + this.answersChoosen.length, result, "success");
    }
    else {
      swal(score + " / " + this.answersChoosen.length, result, "warning");
    }
    this.showResult();
  }

  private showResult() {
    this.answersChoosen.forEach((element, index) => {
      let listAnswer = [this.practice.Questions[index].AnswerA, this.practice.Questions[index].AnswerB, this.practice.Questions[index].AnswerC, this.practice.Questions[index].AnswerD];
      if (element.answer == this.practice.Questions[index].CorrectAnswer) {
        listAnswer.forEach((answer, i) => {
          if (answer == element.answer) {
            $('.icon-correct-' + index + '-' + i).removeClass('hidden');
          }
        });
        $('.question-number-' + index).addClass('green-color');
      } else {
        this.practice.Questions[index].wrongAnswer = element.answer;
        $('.question-number-' + index).addClass('incorrect-result');
      }
    });
    this.practice.Questions.forEach((element, index) => {
      let choice = ['a', 'b', 'c', 'd'];
      let listAnswer = [this.practice.Questions[index].AnswerA, this.practice.Questions[index].AnswerB, this.practice.Questions[index].AnswerC, this.practice.Questions[index].AnswerD];
      listAnswer.forEach((answer, i) => {
        if (answer == element.CorrectAnswer) {
          $('.icon-correct-' + index + '-' + i).removeClass('hidden');
          $('.result-' + index + '-' + choice[i]).addClass('green-color');
        }
        if (this.answersChoosen[index].answer == answer && this.answersChoosen[index].answer != element.CorrectAnswer) {
          console.log(this.answers[index]);
          $('.result-' + index + '-' + choice[i]).addClass('incorrect-result');
          $('.radio-error-' + index + '-' + i).removeClass('colored-success');
          $('.radio-error-' + index + '-' + i).addClass('colored-danger');
        }
      });
    });
  }

  private checkAnswers() {
    let result = true;
    if (this.answersChoosen.length != this.practice.Questions.length) {
      result = false;
    }
    return result;
  }

  private saveScore() {
    this.spinnerService.show();
    this.practiceService.SaveScoreDoPractice(this.doExam).subscribe(
      response => {
        this.toastr.success('Lu im bi tp thnh cng!', 'Yeah!');
        this.showSaveScore = false;
        this.spinnerService.hide();
        this.oldScore = response.LessionPractice.OldScore;
      }, err => {
        this.toastr.error('Some thing went wrong!', 'Oops!');
        this.spinnerService.hide();
      }
    );
  }

  private doAgainClick() {
    this.showSaveScore = false;
    this.doAgain = false;
    this.started = false;
    this.showSubmit = true;
    this.answers = [];
    $('.panel-primary').find('input').attr('disabled', false);
    $('.panel-primary').find('input').attr('checked', false);
    this.totalSeconds = 0;
    this.startDoing();
    this.timeDoingMinutes = "00";
    this.timeDoingSeconds = "00";
    this.answersChoosen = [];
    let choice = ['a', 'b', 'c', 'd'];
    this.practice.Questions.forEach((element, index) => {
      let listAnswer = [this.practice.Questions[index].AnswerA, this.practice.Questions[index].AnswerB, this.practice.Questions[index].AnswerC, this.practice.Questions[index].AnswerD];
      listAnswer.forEach((answer, i) => {
        $('.icon-correct-' + index + '-' + i).addClass('hidden');
        $('.result-' + index + '-' + choice[i]).removeClass('incorrect-result');
        $('.radio-error-' + index + '-' + i).addClass('colored-success');
        $('.radio-error-' + index + '-' + i).removeClass('colored-danger');
        $('.result-' + index + '-' + choice[i]).removeClass('green-color');
      });
      $('.question-number-' + index).removeClass('green-color');
      $('.question-number-' + index).removeClass('incorrect-result');
    });
    this.pageSize = 5;
    this.setPage(1);
  }

  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.questionsPagerService.getPager(this.practice.Questions.length, page, this.pageSize);
    this.pagedItems = this.practice.Questions.slice(this.pager.startIndex, this.pager.endIndex + 1);
    // name="form-field-radio-{{((currentPage-1) * 5) + (i)}}"
    setTimeout(() => {
      this.answersChoosen.forEach((choosen, index) => {
        for (let i = (page - 1) * this.pageSize; i < page * this.pageSize; i++) {
          let nameGroup = "form-field-radio-" + i;
          if (choosen.qNumber == i) {
            $("input[name=" + nameGroup + "][value='" + choosen.answer + "']").attr('checked', 'checked');
          }
        }
      });
    }, 100);

    // set current page is new page
    this.currentPage = page;
  }

  private chooseAnswer(Id: any, answer: string, qNumber: number, typeAnswer: string) {
    let choosen = {
      'Id': Id,
      'answer': answer,
      'qNumber': qNumber,
      'typeAnswer': typeAnswer
    }
    if (!this.checkAnswerChoosen(Id, qNumber, answer)) {
      this.answersChoosen.push(choosen);
    }
  }

  private checkAnswerChoosen(Id: any, question: number, answer: string) {
    let flag = false;
    this.answersChoosen.forEach(element => {
      if (element.qNumber == question) {
        element.answer = answer;
        element.Id = Id;
        flag = true;
      }
    });
    return flag;
  }

}



