import { Component, ViewContainerRef, OnInit } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import {
  PagerService, CategoryService,
  AccountService, ExamQuestionsService
} from '../../_services';
import { error } from 'selenium-webdriver';
@Component({
  selector: 'app-become-an-instructor',
  templateUrl: './become-an-instructor.component.html',
  styleUrls: ['./become-an-instructor.component.css']
})
export class BecomeAnInstructorComponent implements OnInit {
  private learner: any = {};
  constructor(private accountService: AccountService, private http: Http,
    public toastr: ToastsManager, vcr: ViewContainerRef) { }

  ngOnInit() {
    this.getInfoLearner();
  }

  private getInfoLearner() {
    this.accountService.getInfoLearner().subscribe(response => {
      this.learner.FirstName = response.Account.FirstName;
      this.learner.IdLearner = response.Account.Id;
      this.learner.LastName = response.Account.LastName;
      this.learner.PhoneNumber = response.Account.PhoneNumber;
      this.learner.Description = response.ShortInfomation;
    }, error => {
      this.toastr.error('ng nhp trc khi cp nht thng tin!!!');
    });
  }

  private updateLearner() {
    if (this.validForm()) {
      this.accountService.beAnInstructor(this.learner).subscribe(response => {
        this.getInfoLearner();
        this.toastr.success('Cp nht thng tin thnh cng');
      }, error => {
        console.log(error);
      });
    }
  }

  private validForm() {
    if (this.learner.FirstName == '' || this.learner.LastName == '' ||
      this.learner.PhoneNumber == '' || this.learner.Description == '') {
      this.toastr.error('in y  thng tin ca bn');
      return false;
    } else {
      return true;
    }
  }

}



