import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { AppSettings } from '../app.constant';
import 'rxjs/add/operator/map'

@Injectable()
export class PracticeService {
  constructor(private http: Http) { }

  //practice 
  getPractice(id: string) {
    return this.http.get(AppSettings.API_ENDPOINT + 'practices/getById?id=' + id, AppSettings.jwt())
      .map((response: Response) => response.json());
  }

  SaveScoreDoPractice(doExam: any){
    return this.http.post(AppSettings.API_ENDPOINT + 'practices/SaveScoreDoPractice', doExam, AppSettings.jwt())
      .map((response: Response) => response.json());
  }

  // private helper methods

  private jwt() {
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    return new RequestOptions({ headers: headers });
  }
}


